@import url("../../Style/Fonts.css");

header{
    background-color: black;
    position: relative;
    z-index: 1;
    display: flex;
    font-family: "Electrolize", sans-serif;
    overflow-x: hidden;
    
}
.title{
    flex: 1;
    display: flex;
    align-items: center;
    border: 1px solid #FFF;
    color: #FFF;
    font-size: 1.5vw !important; /** need to mark as important, or Fonts.css will override **/
    padding-left: 1vw;
}

.item{
    display: flex;
    align-items: center;
    color: #FFF;
    font-size: 1vw;
    border: 1px solid #FFF;
    border-left: #000;
    padding:.75vw;
}

.icon{
    width: 1vw;
    padding: 1vw;
    border: 1px solid #FFF;
    border-right: #000;
}